@import "reset";
@import "footer";

$background-color:#63b1bc;
body
{
	display:flex;
	flex-wrap:wrap;
}

#main-content
{
	// padding:10%;
    flex: 1 1 80%;
    min-height: 100vh;

	.container
	{
		    padding: 60px 15px 0;
	   .portfolio
	   {
         display: grid;
         height: 100%;
         grid-template-rows: repeat(6,1fr);
         grid-template-columns: repeat(5,1fr);
         grid-gap:20px;

		    .portfolio-item
			{
			  background-color: $background-color;
			  display: flex;
			  justify-content: center;
			  align-items:center; 
			  padding:50px;
              
              &.small
			  {
			  	grid-row: span 1;
			  	grid-column: span 1;

			  }
              
			  &.mediam
			  {
			  	grid-row: span 2;
			  	grid-column: span 2;

			  }
			  &.large
			  {
			  	grid-row:span 3;
			  	grid-column: span 3;

			  }

			  &.tall
			  {
			  	grid-row:span 3;
			  	grid-column:span 2;
			  }

			}
		}
	}

}


